@tailwind base;
@tailwind components;
:root {
  --brandColor: theme("colors.red.500");
  --brandColorDark: theme("colors.red.600");
  --secondaryBrandColor: theme("colors.gray.800");
  --secondaryBrandColorDark: theme("colors.gray.900");
  --textColor: theme("colors.gray.900");
  --textColorInverted: theme("colors.white");
  --ease-in-quad:  cubic-bezier(.55, .085, .68, .53);
  --ease-in-cubic: cubic-bezier(.550, .055, .675, .19);
  --ease-in-quart: cubic-bezier(.895, .03, .685, .22);
  --ease-in-quint: cubic-bezier(.755, .05, .855, .06);
  --ease-in-expo:  cubic-bezier(.95, .05, .795, .035);
  --ease-in-circ:  cubic-bezier(.6, .04, .98, .335);
  --ease-out-quad:  cubic-bezier(.25, .46, .45, .94);
  --ease-out-cubic: cubic-bezier(.215, .61, .355, 1);
  --ease-out-quart: cubic-bezier(.165, .84, .44, 1);
  --ease-out-quint: cubic-bezier(.23, 1, .32, 1);
  --ease-out-expo:  cubic-bezier(.19, 1, .22, 1);
  --ease-out-circ:  cubic-bezier(.075, .82, .165, 1);
  --ease-in-out-quad:  cubic-bezier(.455, .03, .515, .955);
  --ease-in-out-cubic: cubic-bezier(.645, .045, .355, 1);
  --ease-in-out-quart: cubic-bezier(.77, 0, .175, 1);
  --ease-in-out-quint: cubic-bezier(.86, 0, .07, 1);
  --ease-in-out-expo:  cubic-bezier(1, 0, 0, 1);
  --ease-in-out-circ:  cubic-bezier(.785, .135, .15, .86); }

* {
  @apply font-sans; }

img {
  @apply max-w-full; }

body {
  @apply w-full;
  @apply h-full;
  @apply leading-normal; }

table td {
  @apply align-top; }

/* -------------------------------------
    BODY & CONTAINER
------------------------------------- */
body {
  @apply p-0;
  @apply bg-gray-100;
  @apply text-gray-800; }

@screen sm {
  body {
    @apply p-4; } }

.body-wrap {
  @apply w-full;
  @apply bg-gray-100; }

.container {
  display: block !important;
  max-width: 600px !important;
  margin: 0 auto !important;
  /* makes it centered */
  clear: both !important; }

.content {
  @apply block max-w-xl mx-auto p-4; }

/* -------------------------------------
    HEADER, FOOTER, MAIN
------------------------------------- */
.main {
  @apply bg-white;
  @apply rounded; }

.content-wrap {
  @apply p-4; }

.content-block {
  @apply pt-0 px-0 pb-4; }

.footer {
  @apply w-full p-4;
  @apply text-gray-500;
  clear: both; }

.footer p, .footer a, .footer td {
  @apply text-sm text-gray-500; }

/* -------------------------------------
    TYPOGRAPHY
------------------------------------- */
h1, h2, h3 {
  @apply m-0;
  @apply text-black font-black leading-tight; }

h1 {
  @apply text-2xl; }

@screen sm {
  h1 {
    @apply text-3xl; } }

h2 {
  @apply text-xl; }

@screen sm {
  h2 {
    @apply text-2xl; } }

h3 {
  @apply text-lg; }

@screen sm {
  h3 {
    @apply text-xl; } }

h4 {
  @apply text-base; }

@screen sm {
  h4 {
    @apply text-lg; } }

p, ul, ol {
  @apply mb-4;
  @apply font-normal; }

p li, ul li, ol li {
  @apply ml-2; }

/* -------------------------------------
    LINKS & BUTTONS
------------------------------------- */
a {
  @apply text-blue-500 underline; }

.btn-primary {
  @apply inline-block;
  @apply leading-loose text-white font-bold text-center no-underline;
  @apply bg-blue-500;
  @apply border border border-t-4 border-r-8 border-b-4 border-l-8 border-blue-500 rounded;
  @apply cursor-pointer; }
